<template>
	<div class="calc-pass__wrapper">

		<label for="ttk">
			<input id="ttk" type="checkbox"
			@change="changeTTK"
			value="1" 
			:checked="ttk.isChecked"
			>
			Нужен пропуск в ТТК
		</label>

		<label for="sk">
			<input id="sk"type="checkbox"
			@change="changeSK"
			value="2"
			:checked="sk.isChecked"
			>
			Нужен пропуск в СК
		</label>

	</div>
</template>

<script>
	export default {
		props: ['activeCar'],
		data () {
			return {
				ttk: {
					isChecked: true
				},
				sk:{
					isChecked: false
				}
			}
		},
		methods: {
			checkedValue(type) {
				if(type == 'sk') {
					if(this.sk.isChecked) {
						this.sk.value = this.activeCar.sk.value
					}
					else {
						this.sk.value = 0
					}
				}
				else if(type == 'ttk') {
					if(this.ttk.isChecked) {
						this.ttk.value = this.activeCar.ttk.value
					}
					else {
						this.ttk.value = 0
					}
				}
			},
			changeSK() {
				this.sk.isChecked = !this.sk.isChecked
				this.$emit('OnchangeSK', {
					isChecked: this.sk.isChecked
				});
				
			},
			changeTTK() {
				this.ttk.isChecked = !this.ttk.isChecked
				this.$emit('OnchangeTTK', {
					isChecked: this.ttk.isChecked
				});		

			},

		},
		created: function() {
			this.$emit('OnchangeTTK', {
				isChecked: this.ttk.isChecked
			});	
			this.$emit('OnchangeSK', {
				isChecked: this.sk.isChecked
			});	
		}
	}
</script>

<style lang="scss" scoped>

.calc-pass__wrapper {
	display: flex;
	flex-wrap: wrap;
	justify-content: space-between;
	
	label {
		width: 50%;
		text-align: left;
		padding-right: 10px;
		user-select: none;
		display: flex;
		align-items: center;
		font-weight: bold;
		text-transform: uppercase;
		padding-left: 20px;
		margin-top: 20px;

		input {
			margin-right: 10px;
			width: 16px;
			height: 16px;
			position: relative;
			top: -1px;
		}

		&:last-child {
			margin-right: 0;
			padding-left: 10px;
		}
	}
}
</style>


