<template>
	<div class="calc-range__sliders">
		
		<div class="calc-range__slider-wrapper">

			<div class="calc-range__label">
				Время работы автомобиля <span class="calc-range__label-small">(не менее <b>{{activeCar.min_hour}}</b> часов)</span>
			</div>

			<div class="calc-range__slider">
				<button type="button" class="number-input__button number-input__button--minus"
				@click="sliderOptions.valTime == activeCar.min_hour ? sliderOptions.valTime = activeCar.min_hour : sliderOptions.valTime = sliderOptions.valTime - 1"
				:disabled="sliderOptions.valTime == activeCar.min_hour"
				/>

				<vue-slider 
				v-model="sliderOptions.valTime"
				:min="activeCar.min_hour"
				:max="12"
				:processStyle="sliderOptions.processStyle"
				:style="sliderOptions.style"
				:sliderStyle="sliderOptions.sliderStyle"
				:tooltipStyle="sliderOptions.tooltipStyle"
				:width="sliderOptions.width"
				@input="onValTime"
				></vue-slider>

				<button type="button" class="number-input__button number-input__button--plus"
				@click="sliderOptions.valTime == 12 ? sliderOptions.valTime = 12 : sliderOptions.valTime = sliderOptions.valTime + 1"
				:disabled="sliderOptions.valTime == 12"
				/>
			</div>

		</div>

		<div class="calc-range__slider-wrapper">
			<div class="calc-range__label">
				Расстояние за МКАД <span class="calc-range__label-small">(1 км - <b>{{activeCar.mkad_price}}</b> руб)</span>
			</div>
			<div class="calc-range__slider">

				<button type="button" class="number-input__button number-input__button--minus"
				@click="sliderOptions.valMKAD == 0 ? sliderOptions.valMKAD == 0 : sliderOptions.valMKAD = sliderOptions.valMKAD - 1"
				:disabled="sliderOptions.valMKAD == 0"
				>

				</button> 

				<vue-slider 
				v-model="sliderOptions.valMKAD"
				:min="0"
				:max="300"
				:processStyle="sliderOptions.processStyle"
				:style="sliderOptions.style"
				:sliderStyle="sliderOptions.sliderStyle"
				:tooltipStyle="sliderOptions.tooltipStyle"
				:width="sliderOptions.width"
				@input="onValMKAD"
				></vue-slider>

				<button type="button" class="number-input__button number-input__button--plus"
				@click="sliderOptions.valMKAD == 300 ? sliderOptions.valMKAD == 300 : sliderOptions.valMKAD = sliderOptions.valMKAD + 1"
				:disabled="sliderOptions.valMKAD == 300"
				></button>

			</div>

		</div>

	</div>

</template>

<script>
	import vueSlider from 'vue-slider-component';

	export default {
		components: {
			vueSlider
		},
		props: ['activeCar'],
		data () {
			return {
				sliderOptions: {
					width: '69%',
					valTime: 0,
					valMKAD: 0,
					processStyle: {
						borderRadius: 0,
						background: '#41b883'				
					},
					style: {
						"marginLeft": "0px"
					},
					sliderStyle: {
						width: '35px',
						height: '35px',
						borderRadius: '0px',
						backgroundColor: '#35495e',
						boxShadow: '0px 2px 6px 0 rgba(1, 1, 1, 0.25)',
						top: '-14.5px',
						bottom: 'auto',
						left: '-9.5px',
					},
					tooltipStyle: {
						width: '100%',
						height: '100%',
						borderRadius: "0",
						border: "none",
						overflow: "hidden",
						backgroundColor: 'transparent',
						top: '0',
						left: '0',
						position: 'absolute',
						display: 'flex',
						alignItems: 'center',
						justifyContent: 'center',
						color: '#fff',
						fontWeight: 'bold',
						fontSize: '16px',
						padding: '0'
					}
				}
			}
		},
		methods: {
			onValTime(){
				this.$emit('onValTime', {
					value: this.sliderOptions.valTime
				});
			},
			onValMKAD(){
				this.$emit('onValMKAD', {
					value: this.sliderOptions.valMKAD
				});
			}
		},
		created: function () {
			this.sliderOptions.valTime = this.activeCar.min_hour
			this.onValTime
		}
	}
</script>

<style lang="scss">

@import '../assets/scss/_variables.scss';

.calc-range{
	&__sliders {
		display: flex;
		flex-wrap: wrap;
		width: 100%;
	}

	&__slider {
		display: flex;
		align-items: center;
		justify-content: space-between;
		width: 48%;

		&-wrapper {
			display: flex;
			width: 100%;
			justify-content: space-between;
			align-items: center;
			margin: 20px 20px 10px;

			&:last-child {
				margin-top: 0px;
				margin-bottom: 0;
				padding-bottom: 30px;
				border-bottom: 1px solid rgba($black, .3);
			}
		}
	}

	&__label {
		display: flex;
		flex-direction: column;
		align-items: flex-start;
		text-transform: uppercase;
		font-weight: bold;

		&-small {
			display: block;
			text-transform: lowercase;
			font-weight: normal;
		}
	}
}

.vue-slider-component .vue-slider-dot.vue-slider-always .vue-slider-tooltip-wrap {
	display: flex !important;
	width: 100%;
	height: 100%;
	top: 100%;
	position: absolute;
	align-items: center;
	justify-content: center;
}


</style>


