<template>
	<div class="calc-choose-final">
		<h5><mark>Вы</mark> выбрали:</h5>

		<ul class="calc-choose-final__wraper">

			<li>{{typeRelocation}}</li>

			<transition name="slide">
				<li v-if="currentCity.name">Пункт назначения: <span>{{currentCity.name.cityName}}</span></li>
			</transition>

			<transition name="slide">
				<li v-if="(activeCar.ttk.isChecked && stateRelocation == 'moscow')">Нужен пропуск в ТТК</li>			
			</transition>

			<transition name="slide">
				<li v-if="(activeCar.sk.isChecked && stateRelocation == 'moscow')">Нужен пропуск в СК</li>			
			</transition>

			<transition name="slide">
				<li v-if="stateRelocation == 'moscow'">Время работы автомобиля: <span>{{activeCar.valTime}} <span class="ttl">ч</span></span></li>
			</transition>
			
			<transition name="slide">
				<li v-if="stateRelocation == 'moscow'">Расстояние за МКАД: <span>{{activeCar.valMKAD}} <span class="ttl">км</span></span></li>
			</transition>
			
			<li>Автомобиль: <span>{{activeCar.name}}</span></li>

			<transition name="slide">
				<li v-if="showTitleFurniture">Перевозим:
					<transition-group name="slide-left">
						<span class="item-parent" v-for="(item,index) in furnitureArray" v-if="item[2] !== 0" :key="index">
							{{item[0]}}: <span class="ttl">x</span>{{item[2]}}<span class="comma">,</span> 
						</span>
					</transition-group>		
					
				</li>
			</transition>

			<transition name="slide">
				<li v-if="loaderChecked > 0">Грузчики: <span>{{loaderChecked}}</span></li>
			</transition>

			<transition name="slide">
				<li v-if="loaderTime > 0">Время работы грузчиков: <span>{{loaderTime}} <span class="ttl">ч</span></span></li>
			</transition>

			<transition name="slide">
				<li class="item-materials" v-if="showTitleMaterials">Упаковочные материалы:
					<br>
					<transition-group name="slide">
						<span class="item-parent" v-for="(item,index) in materials" v-if="item.select !== 0" :key="index">
							{{item.name.replace(", шт.", "").replace(", рул.", "")}}:
							<span class="ttl">x</span>{{item.select}}
							<span class="small ttl">({{item.price}} &#8381;)</span><span class="comma">,</span>
							<br>
						</span>
					</transition-group>
				</li>
			</transition>
			

		</ul>

		<div class="calc-choose-final__price">
			<h5><mark>Примерная</mark> стоимость</h5>
			<h2><mark>{{finalPrice}}</mark> руб.</h2>

			<form @submit.prevent="">
				<input type="text" name="name" placeholder="Имя" autocomplete="off">
				<!-- <input type="number" name="tel" placeholder="Телефон" autocomplete="off"> -->
				<masked-input mask="\+\7 (111) 1111-11" placeholder="Телефон" />
				<button >Заказать</button>
			</form>
		</div>
	</div>
</template>


<script>
	import MaskedInput from 'vue-masked-input'

	export default {
		components: {
			MaskedInput
		},
		props: ['typeRelocation','activeCar', 'furnitureArray', 'showTitleFurniture','loaderTime','loaderChecked','materials','showTitleMaterials', 'finalPrice', 'stateRelocation','currentCity'],
		data () {
			return {
				furnitureFinal: []
			}
		},
		methods: {
			onFurnitureFinal(){
				this.furnitureArray = this.furnitureFinal
			}
		}
	}
</script>


<style lang="scss" scoped>
@import '../assets/scss/_variables.scss';

.slide-enter-active, .slide-leave-active {
	transition: all .5s ease;
	max-height: 350px;
	opacity: 1;
}
.slide-enter, .slide-leave-to  {
	max-height: 0;
	transition: all .5s ease;
	opacity: 0;
}


.slide-left-enter-active, .slide-left-leave-active {
	transition: all .5s ease;
	max-width: 300px;
}
.slide-left-enter, .slide-left-leave-to  {
	max-width: 0;
	transition: all .5s ease;
	opacity: 0;
}


mark {
	background-color: transparent;
	color: $accent;
	padding: 0;
}

h5 {
	text-transform: uppercase;
	font-weight: bold;
	font-size: 30px;
}
h2 {
	font-weight: bold;
	font-size: 36px;
	mark {
		font-size: 60px;
		text-transform: uppercase;
		position: relative;
		top: 3px;
	}
}
span {
	&.small {
		font-size: 13px;
	}
	&.ttl {
		text-transform: lowercase;
		display: inline-flex;
		align-items: center;
	}
}
.calc-choose {
	&-final {
		text-align: left;
		box-sizing: border-box;
		background: #f7f7f7;
		padding: 32px 22px;
		box-shadow: 0 5px 5px 0 rgba(0,0,0,0.32);
		height: 100%;
		display: flex;
		flex-direction: column;

		&__wraper {
			list-style-type: none;
			padding-left: 0;
			margin-top: 28px;

			li {
				padding-left: 0;
				text-transform: uppercase;
				font-weight: bold;
				margin-bottom: 0px;
				
				b {
					font-weight: bold;
					text-transform: uppercase;
				}
				&:last-child {
					margin-bottom: 0;
				}

				span {
					font-weight: normal;

					&.item-parent {
						display: inline-flex;
						overflow: hidden;
						white-space: nowrap;
						margin-right: 3px;

						> span {
							margin-left: 3px;
							&:last-child {
								margin-left: 0;
							}
						}
						&:last-child {
							.comma {
								display: none;
							}	
						}
					}
				}

				&.item-materials {
					> span {
						display: flex;
						flex-direction: column;
					}
				}
			}
		}
		&__price {
			margin-bottom: 0;
			margin-top: auto;

			form {
				input, button {
					width: 100%;
					height: 50px;
					margin-top: 20px;
					font-weight: bold;
				}
				input {
					padding-left: 25px;
					&:focus {
						outline-color: $accent;
					}
				}
				button {
					background-color: $accent;
					text-transform: uppercase;
					font-size: 19px;
					height: 48px;
					color: #fff;
					border: 2px solid $accent;
					transition: all .5s ease;

					&:hover {
						background: $black;
						color: #fff;
						border-color: $black;
					}
				}
			}
		}
	}
}
</style>


