<template>
	<div class="calc-car__select-wrapper">
		<div class="calc-car__left">
			<transition name="fade">
				<img :src="activeCar.img" alt="">
			</transition>
		</div>
		<div class="calc-car__right">
			<h5>Выберите автомобиль</h5>
			<v-select 
			v-model="carsSelect" 
			:options="options" 
			label="name" 
			:searchable="false"
			:clearSearchOnSelect="false"
			:filterable="false"
			@input="onSelectActive">
			<template slot="cars" slot-scope="cars">
				{{ cars.name }}
			</template>
		</v-select>

		<div class="calc-car__params">
			<span>Грузоподъёмность: <mark>{{ activeCar.carrying }} кг</mark></span>
			<span>Количество палет: <mark>{{ activeCar.pallets }} шт</mark></span>
		</div>
	</div>
</div>
</template>

<script>
	import vSelect from 'vue-select'
	export default {
		components: {
			vSelect
		},
		props: ['activeCar', 'cars'],
		data () {
			return {
				carsSelect: this.activeCar.name,
				selected: null,
				options: this.cars
			}
		},
		methods: {
			onSelectActive(val) {
				let index = 0
				for (var i=0; i < this.cars.length; i++) {
					if (this.cars[i].name == val.name) {
						index = i
					}
				};
				this.$emit('OnSelectActiveParent', {
					selectedIndex: index
				});
				
			}
		},
		created: function() {
		}
	}
</script>

<style lang="scss" scoped>
@import '../assets/scss/_variables.scss';

.calc {

	&-car {

		&__params {
			display: flex;
			flex-direction: column;
			align-items: flex-start;

			span {
				font-size: 15px;
				font-weight: bold;
				text-transform: uppercase;
			}

			mark {
				background-color: transparent;
				color: $accent;
				font-weight: bold;
				font-size: 15px;
			}

		}

		&__select-wrapper {
			display: flex;
			margin-top: 20px;
		}

		&__left,
		&__right {
			width: 50%;
		}

		&__right {
			display: flex;
			flex-wrap: wrap;
			align-items: flex-start;
			flex-direction: column;
			justify-content: flex-start;
			padding-left: 10px;
			padding-right: 20px;
		}

		&__left {
			height: 170px;
			padding-right: 10px;

			img {
				height: 100%;
				width: 100%;
				object-fit: contain;
				max-width: 250px;
			}
		}

		&__select {
			text-align: left;
			margin-bottom: 10px;
			padding: 8px;
			width: 100%;
		}
	}
}

h5 {
	text-align: left;
	margin-bottom: 10px;
	font-weight: bold;
	text-transform: uppercase;
} 

</style>


