<template>
	<div class="calc-destination">
		<div class="calc-destination__wrapper">
			<h5>Выберите пункт назначения</h5>
			<v-select 
			v-model="currentCity.name" 
			:options="cities"
			label="cityName"
			:searchable="false"
			:clearSearchOnSelect="false"
			:filterable="false"
			:placeholder="'Выберите город'"
			@input="cityDistanceChange">
		</v-select>
	</div>
	<transition name="fade">
		<div v-if="currentCity.name" class="calc-destination__select">
			<div class="calc-destination__select-wrapper">
				<h5>Маршрут:</h5>
				<span class="calc-destination__city">
					Москва

					<svg xmlns="http://www.w3.org/2000/svg" xmlns:xlink="http://www.w3.org/1999/xlink" version="1.1" id="Capa_1" x="0px" y="0px" width="30" height="20" viewBox="0 0 268.832 268.832" style="enable-background:new 0 0 268.832 268.832;" xml:space="preserve">
						<g>
							<path d="M265.171,125.577l-80-80c-4.881-4.881-12.797-4.881-17.678,0c-4.882,4.882-4.882,12.796,0,17.678l58.661,58.661H12.5   c-6.903,0-12.5,5.597-12.5,12.5c0,6.902,5.597,12.5,12.5,12.5h213.654l-58.659,58.661c-4.882,4.882-4.882,12.796,0,17.678   c2.44,2.439,5.64,3.661,8.839,3.661s6.398-1.222,8.839-3.661l79.998-80C270.053,138.373,270.053,130.459,265.171,125.577z" fill="#41b883"/>
						</g>
					</svg>

					{{ currentCity.name.cityDistance }} км

					<svg xmlns="http://www.w3.org/2000/svg" xmlns:xlink="http://www.w3.org/1999/xlink" version="1.1" id="Capa_2" x="0px" y="0px" width="30" height="20" viewBox="0 0 268.832 268.832" style="enable-background:new 0 0 268.832 268.832;" xml:space="preserve">
						<g>
							<path d="M265.171,125.577l-80-80c-4.881-4.881-12.797-4.881-17.678,0c-4.882,4.882-4.882,12.796,0,17.678l58.661,58.661H12.5   c-6.903,0-12.5,5.597-12.5,12.5c0,6.902,5.597,12.5,12.5,12.5h213.654l-58.659,58.661c-4.882,4.882-4.882,12.796,0,17.678   c2.44,2.439,5.64,3.661,8.839,3.661s6.398-1.222,8.839-3.661l79.998-80C270.053,138.373,270.053,130.459,265.171,125.577z" fill="#41b883"/>
						</g>
					</svg>

				{{ currentCity.name.cityName }}</span>

			</div>
		</div>
	</transition>
</div>
</template>

<script>
	import vSelect from 'vue-select'
	export default {
		props: ['activeCar','currentCity'],
		components: {
			vSelect
		},
		data () {
			return {
				citySelect: '',
				cities: [
				{ cityName: "Санкт-Петербург", cityDistance: 650 },
				{ cityName: "Казань", cityDistance: 820 },
				{ cityName: "Краснодар", cityDistance: 1360 },
				{ cityName: "Крым", cityDistance: 1400 },
				{ cityName: "Нижний Новгород", cityDistance: 420 },
				{ cityName: "Самара", cityDistance: 1050 },
				{ cityName: "Сочи", cityDistance: 1630 },
				{ cityName: "Ярославль", cityDistance: 275 },
				{ cityName: "Рязань", cityDistance: 200 },
				{ cityName: "Смоленск", cityDistance: 400 }
				]
			}
		},
		methods: {
			cityDistanceChange() {
				this.$emit('onCityDistanceChange', {
					city: this.currentCity.name.cityName
				});
			}
		},
		created: function () {
			console.log(this.citySelect)
		}
	}
</script>

<style lang="scss">

.calc-destination {
	.v-select.unsearchable input[type=search] {
		opacity: 1 !important;
		font-weight: normal;
	}

	&__wrapper {
		padding: 35px 20px;
		display: flex;
		justify-content: space-between;
		align-items: center;
		
		.v-select {
			width: 42% !important;
			margin: 0 !important;
		}
	}
	&__select {
		&-wrapper {
			display: flex;
			align-items: center;
			padding: 0 20px 20px;
			font-size: 18px;
		}
		span {
			position: relative;
			bottom: 1px;
		}
	}
	h5 {
		margin:0;
		text-transform: uppercase;
		font-weight: bold;
		margin-right: 20px;
	}
	#Capa_1 {
		transform: scale(-1,-1)
	}
	svg {
		position: relative;
		top: 3px;
	}
}

</style>


