<template>
	<div class="calc-packaging">
		<h5>Упаковочные материалы</h5>

		<div class="calc-packaging__wrapper">
			<div class="calc-packaging__item" v-for="(material, index) in materials">
				<h6>{{material.name}}</h6>
				<number-input 
				:value="material.value" 
				inline center controls
				:min="0"
				:max="material.max"
				:step="1"
				@change="materialsChange" 
				v-model="materials[index].select"
				></number-input>
			</div>
		</div>

	</div>
</template>

<script>
	import numberInput from '@chenfengyuan/vue-number-input';

	export default {
		props: ['materials'],
		components: {
			numberInput
		},
		data () {
			return {
			}
		},
		methods: {
			materialsChange() {
				this.$emit('onMaterialsChange', {
					materials: this.materials
				})
			}
		},
		created: function () {
		}
	}
</script>

<style lang="scss" scoped>

h5 {
	text-transform: uppercase;
	font-weight: bold;
	margin-top: 30px;
	margin-bottom: 20px;
	text-align: left;
}

.calc-packaging{
	padding: 0 20px;
	&__wrapper {
		display: flex;
		flex-wrap: wrap;
		justify-content: space-between;
	}

	&__item {
		width: 48%;
		margin-bottom: 20px;

		&:nth-child(3),
		&:nth-child(4) {
			margin-bottom: 0;
		}

		h6 {
			text-transform: uppercase;
			font-weight: bold;
			text-align: left;
		}
	}
}
</style>


