<template>
	<div class="calc-loaders">
		<h5>Грузчики</h5>

		<div class="calc-loaders__params">

			<div class="calc-loaders__count">
				<h6>Выберите количество гручиков</h6>
				
				<div class="calc-loaders__count-wrapper">
					<button type="button" class="number-input__button number-input__button--plus"
					@click="loaderChecked == loaderCountEl.length ? loaderChecked == loaderCountEl.length : loaderChecked = loaderChecked + 1, loaderChange()"
					:disabled="loaderChecked == loaderCountEl.length"
					></button>

					<template v-for="n in loaderCountEl.slice().reverse()">
						<input 
						:id="n"
						type="radio"
						:value="n"
						v-model="loaderChecked"
						@change="loaderChange"
						>		
						<label :for="n">
							<svg id="user_grey" viewBox="0 0 409.165 409.164">
								<g>
									<path d="M204.583,216.671c50.664,0,91.74-48.075,91.74-107.378c0-82.237-41.074-107.377-91.74-107.377    c-50.668,0-91.74,25.14-91.74,107.377C112.844,168.596,153.916,216.671,204.583,216.671z" fill="#bbbbbc"/>
									<path d="M407.164,374.717L360.88,270.454c-2.117-4.771-5.836-8.728-10.465-11.138l-71.83-37.392    c-1.584-0.823-3.502-0.663-4.926,0.415c-20.316,15.366-44.203,23.488-69.076,23.488c-24.877,0-48.762-8.122-69.078-23.488    c-1.428-1.078-3.346-1.238-4.93-0.415L58.75,259.316c-4.631,2.41-8.346,6.365-10.465,11.138L2.001,374.717    c-3.191,7.188-2.537,15.412,1.75,22.005c4.285,6.592,11.537,10.526,19.4,10.526h362.861c7.863,0,15.117-3.936,19.402-10.527    C409.699,390.129,410.355,381.902,407.164,374.717z" fill="#bbbbbc"/>
								</g>
							</svg>
						</label>	
					</template>

					<button type="button" class="number-input__button number-input__button--minus"
					@click="loaderChecked == 0 ? loaderChecked == 0 : loaderChecked = loaderChecked - 1, loaderChange()"
					:disabled="loaderChecked == 0"
					></button>
				</div>
			</div>

			<div class="calc-loaders__time">
				<h6>Время работы грузчиков, часов</h6>
				<div class="calc-loaders__time-wrapper">
					<number-input 
					:value="0" 
					inline center controls
					:min="loaderChecked == 0 ? 0 : 3"
					:max="12"
					:step="1"
					:disabled="loaderChecked == 0"
					@change="loaderTimeChange" 
					v-model="loaderTime"
					></number-input>
				</div>

			</div>

		</div>
	</div>
</template>

<script>
	import numberInput from '@chenfengyuan/vue-number-input';

	export default {
		components: {
			numberInput
		},
		data () {
			return {
				loaderChecked: 0,
				loaderCountEl: 8,
				loaderTime: 0
			}
		},
		methods: {
			loaderChange(){
				this.loaderChecked == 0 ? this.loaderTime = 0 : this.loaderTime > 0 ? this.loaderTime = this.loaderTime : this.loaderTime = 3

				this.$emit('onLoaderChange', {
					loaderChecked: this.loaderChecked,
					loaderTime: this.loaderTime
				});
			},
			loaderTimeChange(){
				this.$emit('onLoaderTimeChange', {
					loaderTime: this.loaderTime
				});
			}
		},
		created: function () {
			let labelArr = []
			for(let i = 1; i <= this.loaderCountEl; i++) {
				labelArr.push(i)
			}
			this.loaderCountEl = labelArr
			this.loaderTime = this.loaderChecked == 0 ? 0 : 3

		}
	}
</script>

<style lang="scss" scoped>
@import '../assets/scss/_variables.scss';

.calc{
	&-loaders{
		
		
		margin-left:20px;
		margin-right: 20px;

		&__params {
			display: flex;
			align-items: center;
			justify-content: space-between;
			padding-bottom: 30px;
			border-bottom: 1px solid rgba($black, .3);

			label {
				width: 20px;
				height: 20px;
				margin: 0 3px;
				color: #fff;
				cursor: pointer;

				svg {
					width: 100%;
					height: 100%;
				}
			}

			input[type="radio"] {
				display: none;

				&:checked ~ label {

					svg {

						path {
							fill: $accent;
						}
					}
				}
			}

		}

		&__count{
			width: 48%;

			&-wrapper {
				display: flex;
				flex-wrap: wrap;
				align-items: center;
				flex-direction: row-reverse;
				justify-content: space-between;
			}
		}

		&__time {
			width: 48%;

			input {
				&:disabled {
					opacity: 1 !important;
				}
			}
		}
	}
}

h5 {
	text-transform: uppercase;
	font-weight: bold;
	margin-top: 30px;
	margin-bottom: 20px;
	text-align: left;
}

h6 {
	text-transform: uppercase;
	font-weight: bold;
}


</style>


