<template>
  <div class="calc-button__wrapper">
    <button class="calc-button" :class="{ active: btnMoscow }" @click="btnMoscowClick">Перевозка по Москве</button>
    <button class="calc-button" :class="{ active: btnRussia }" @click="btnRussiaClick">Перевозка по России</button>
  </div>
</template>

<script>
  export default {
    data () {
      return {
        btnMoscow: true,
        btnRussia: false,
      }
    },
    methods: {
      btnMoscowClick() {
        this.btnMoscow = true;
        this.btnRussia = false;

        this.$emit('relocationMoscow', {
          title: 'Перевозка по Москве',
          state: 'moscow'
        });
      },
      btnRussiaClick() {
        this.btnMoscow = false;
        this.btnRussia = true;

        this.$emit('relocationRussia', {
          title: 'Перевозка по России',
          state: 'russia'
        });
      }
    },

    created: function(){
      if(this.btnMoscow) {
       this.$emit('relocationMoscow', {
         title: 'Перевозка по Москве',
         state: 'moscow'
       });
     }
     else if(this.btnRussia) {
      this.$emit('relocationRussia', {
       title: 'Перевозка по России',
       state: 'russia'
     });
    }
  }
}
</script>

<style lang="scss">
@import '../assets/scss/_variables.scss';
$bd_top_header: 4px;

.calc {
  &-button {
    text-transform: uppercase;
    min-width: 0;
    width: 100%;
    font-size: 16px;
    font-weight: bold;
    color: #fff;
    height: 50px;
    transition: all .25s ease;
    background-color: $black;
    border-bottom: $bd_top_header solid $accent;
    border-top: $bd_top_header solid $black;
    &.active {
      background-color: $accent;
      border-top-color: $accent;
    }
    &__wrapper {
      display: flex;
    }
  }
}
</style>


