<template>
	<div class="calc-choose__wrapper" :class="{ active : showBlock }">
		<h5 @click="showBlock = !showBlock">Не знаете как выбрать автомобиль? Укажите объём или перечислите вещи</h5>
		<transition name="fade">
			<div class="calc-choose__label_wrapper" v-show="showBlock">
				<div class="calc-choose__label" v-for="(value, key, index) in furnitureAmount.furniture">
					<span class="calc-choose__label-header">
						{{value[0]}} шт.
					</span>

					<span class="calc-choose__label-input_wrapper">
						<number-input 
						:value="0" 
						inline center controls
						:min="0"
						:max="50"
						:step="1"
						@change="addVal(), onChooseHelp()" 
						v-model="furnitureAmount.furniture[key][2]"
						></number-input>
					</span>

				</div>

				<div class="calc-choose__label-indicate">
					Укажите примерный объём груза, м. куб.
					<number-input 
					:value="0" 
					inline center controls
					:min="0"
					:max="300"
					:step="1"
					@change="onChooseHelp()" 
					v-model="furnitureAmount.furnitureTotalSum"
					></number-input>
				</div>	

				<div class="calc-choose__recomended">
					На основании указанных данных рекомендуется:
					<span class="calc-choose__recomended-car">
						{{ recomendedCar }}
					</span>
				</div>

			</div>
		</transition>
	</div>

</template>


<script>
	import numberInput from '@chenfengyuan/vue-number-input';

	export default {
		props: ['activeCar', 'cars'],
		components: {
			numberInput
		},
		data () {
			return {
				showBlock: false,
				recomendedCar: this.activeCar.name,
				furnitureAmount: {
					furniture: [
					['шкаф', 1.5, 0],
					['диван', 2, 0],
					['стол', 0.5, 0],
					['стул', 0.5, 0],
					['кровать', 3, 0],
					['кресло', 1.5, 0],
					],
					furnitureTotalSum: 0,
					chooseHelpIndex: 0,
				}
			}
		},
		methods: {
			addVal(){
				let furniture = this.furnitureAmount.furniture
				let furnitureTotalSum = 0

				let arr_1 = []
				let arr_2 = []

				for (var item in furniture) {
					arr_1.push(furniture[item][1])
					arr_2.push(furniture[item][2])
				}

				arr_1.forEach(function(item,i) {
					let arr_2_Value = arr_2[i]
					furnitureTotalSum += (arr_2_Value * item)
				})

				this.furnitureAmount.furnitureTotalSum = furnitureTotalSum

				this.$emit('OnAddHelpValue', {
					furnitureArray: this.furnitureAmount.furniture
				})
			},
			onChooseHelp() {
				let furnitureTotalSum = this.furnitureAmount.furnitureTotalSum
				let parentIndex
				let array = [];

				this.cars.forEach(function (car, index) {
					array.push(car.amount)
				});

				function closest(array,num){
					var i=0;
					var minDiff=1000;
					var ans;
					for(i in array){
						var m=Math.abs(num-array[i]);
						if(m<minDiff){ 
							minDiff=m; 
							ans=array[i]; 
						}
					}
					return ans;
				}

				for (var i=0; i < this.cars.length; i++) {
					if (this.cars[i].amount == closest(array,furnitureTotalSum)) {
						parentIndex = i
					}
				};
				this.chooseHelpIndex = parentIndex

				this.$emit('OnSelectActiveParent', {
					selectedIndex: this.chooseHelpIndex
				})

				this.recomendedCar = this.activeCar.name
			}

		},
		created: function() {
		}
	}
</script>


<style lang="scss">
@import '../assets/scss/_variables.scss';


.calc {
	&-choose {

		&__wrapper {
			display: flex;
			flex-wrap: wrap;
			justify-content: center;
			width: 100%;
			background: #fff;
			padding: 0 20px 0;
			overflow: hidden;
			transition: all .5s ease;

			&.active {
				background: $grey_l;
			}
			h5 {
				margin-top: 20px;
				margin-bottom: 20px;
				width: 100%;
				font-size: 15px;
				text-decoration: underline;
				color: $accent;
				cursor: pointer;
				user-select: none;
				text-align: left;
			}


		}

		&__label{
			display: flex;
			flex-wrap: wrap;
			justify-content: center;
			margin-right: 20px;
			width: 100%;
			max-width: 142px;
			margin-bottom: 20px;
			&:last-child {
				margin-right: 0;
			}
			&:nth-last-of-type(2) {
				margin-right: 0;
			}
			&:nth-child(3) {
				margin-right: 0;
			}
			&:nth-child(6) {
				margin-right: 0;
			}
			input {
				text-align: center;
				font-size: 30px;
				font-weight: bold;
				width: 100%;
				height: auto;
				background-color: transparent;
				border: none;
				&:focus,
				&:hover,
				&:active {
					outline: none;
					user-select: none;
				}
			}
			&_wrapper {
				display: flex;
				flex-wrap: wrap;
				justify-content: space-between;
				width: 100%;
			}

			&-indicate {
				display: flex;
				width: 100%;
				flex-direction: row;
				justify-content: space-between;
				align-items: center;
				margin-top: 20px;
				margin-right: 0;
				margin-left: auto;
				text-transform: uppercase;
				font-weight: bold; 
				input {
					margin-left: 0;
					text-align: center;
					width: calc((100% / 3) - 20px);
					font-weight: bold;
				}
			}
			&-header {
				text-transform: uppercase;
				text-align: left;
				font-weight: bold;
				width: 100%; 
			}

			&-input{
				&_wrapper {
					display: flex;
					align-items: center;
					justify-content: space-between;
				}
			}
		}

		&__recomended {
			margin-left: 0;
			margin-top: 20px;
			margin-bottom: 20px;
			margin-right: auto;

			&-car {
				margin-top: 0px;
				font-weight: bold;
				text-transform: uppercase;
				text-align: left;
			}
		}


	}

}

</style>


