<template>
	<div id="app" class="calc container">
		<div class="vue-img-logo">
			<mark>Калькулятор</mark> грузоперевозок <mark>Vue</mark> JS
			<img src="./assets/logo.png">
		</div>

		<div class="calc__left">

			<RouteBtnToggle
			@relocationMoscow="onRelocationMoscow"
			@relocationRussia="onRelocationRussia"
			/>


			<transition name="fade">
				<Destination v-if="stateRelocation == 'russia'"
				:activeCar="activeCar"
				:currentCity="currentCity"
				@onCityDistanceChange="cityDistanceChange"
				/>
			</transition>

			<ChooseHelp
			:cars="cars"
			:activeCar="activeCar"
			@OnSelectActiveParent="SelectActiveParent"
			@OnAddHelpValue="AddHelpValue"
			/>

			<CarSelect
			:activeCar="activeCar"
			:cars="cars"
			@OnSelectActiveParent="SelectActiveParent"
			/>

			<transition name="fade">
				<PassCheckbox v-if="stateRelocation == 'moscow'"
				@OnchangeTTK="changeTTK"
				@OnchangeSK="changeSK"
				:activeCar="activeCar"
				/>
			</transition>

			<transition name="fade">
				<RangeSliders v-if="stateRelocation == 'moscow'"
				:activeCar="activeCar"
				@onValTime="ValTime"
				@onValMKAD="ValMKAD"
				/>
			</transition>

			<Loaders
			@onLoaderChange="loaderChange"
			@onLoaderTimeChange="loaderTimeChange"
			/>

			<PackagingMaterials
			:materials="materials"
			@onMaterialsChange="materialsChange"
			/>
			
		</div>

		<div class="calc__right">
			<ChooseFinalList
			:typeRelocation="typeRelocation"
			:activeCar="activeCar"
			:furnitureArray="furnitureArray"
			:showTitleFurniture="showTitleFurniture"
			:showTitleMaterials="showTitleMaterials"
			:loaderChecked="loaderChecked"
			:loaderTime="loaderTime"
			:materials="materials"
			:finalPrice="finalPrice"
			:stateRelocation="stateRelocation"
			:currentCity="currentCity"
			/>
		</div>
	</div>
</template>

<script>
	import RouteBtnToggle from './components/RouteBtnToggle'
	import ChooseHelp from './components/ChooseHelp'
	import CarSelect from './components/CarSelect'
	import PassCheckbox from './components/PassCheckbox'
	import RangeSliders from './components/RangeSliders'
	import Loaders from './components/Loaders'
	import PackagingMaterials from './components/PackagingMaterials'
	import ChooseFinalList from './components/ChooseFinalList'
	import Destination from './components/Destination'
	

	import cars_arr from './components/Cars_arr.json'
	import materials from './components/Materials.json'

	export default {
		name: 'App',
		components: {
			RouteBtnToggle,
			ChooseHelp,
			CarSelect,
			PassCheckbox,
			RangeSliders,
			Loaders,
			PackagingMaterials,
			ChooseFinalList,
			Destination
		},
		data() {
			return {
				cars: cars_arr,
				materials: materials,
				typeRelocation: '',
				stateRelocation: 'moscow',
				furnitureArray: [],
				showTitleFurniture: false,
				showTitleMaterials: false,
				loaderChecked: 0,
				loaderTime: 0,
				finalPrice: 2200,
				oneLoaderPrice: 150,
				totalMaterialsPrice: 0,
				currentCity: {
					name: '',
					price: 0
				},
				activeCar: {
					"index": 0,
					"name": cars_arr[0].name,
					"carrying": cars_arr[0].carrying,
					"img": cars_arr[0].img,
					"pallets": cars_arr[0].pallets,
					"min_hour": cars_arr[0].min_hour,
					"worth_of_hour": cars_arr[0].worth_of_hour,
					"mkad_price": cars_arr[0].mkad_price,
					"ttk": {
						isChecked: false,
						value: cars_arr[0].sk
					},
					"sk":{
						isChecked: false,
						value: cars_arr[0].sk
					},
					"valTime": 0,
					"valMKAD": 0,
					"city": ''
				}
			}
		},
		methods: {
			carActive(index) {
				index = this.activeCar.index

				this.activeCar.name = this.cars[index].name
				this.activeCar.img = this.cars[index].img
				this.activeCar.carrying = this.cars[index].carrying
				this.activeCar.pallets = this.cars[index].pallets
				this.activeCar.min_hour = this.cars[index].min_hour
				this.activeCar.worth_of_hour = this.cars[index].worth_of_hour
				this.activeCar.mkad_price = this.cars[index].mkad_price
				this.activeCar.ttk.value = this.cars[index].ttk
				this.activeCar.sk.value = this.cars[index].sk
				this.activeCar.min_car_price = this.cars[index].min_car_price
				this.currentCity.price = this.cars[this.activeCar.index].cities[this.currentCity.name.cityName]

				this.activeCar.ttk.isChecked ? this.activeCar.ttk.value = this.cars[index].ttk : this.activeCar.ttk.value = 0 
				this.activeCar.sk.isChecked ? this.activeCar.sk.value = this.cars[index].sk : this.activeCar.sk.value = 0


			},

			SelectActiveParent(data) {
				this.activeCar.index = data.selectedIndex
				this.carActive()
				this.finalPriceSum()
			},

			AddHelpValue(data) {
				let dataFurnitureArray = data.furnitureArray 
				let furnitureArray = this.furnitureArray
				let showTitleFurniture 

				this.furnitureArray = data.furnitureArray

				this.furnitureArray.forEach(function(item, index){
					if(item[2] > 0) {
						showTitleFurniture = true
					}
				});

				this.showTitleFurniture = showTitleFurniture
				this.finalPriceSum()
			},

			changeSK(data) {
				let index = this.activeCar.index
				this.activeCar.sk.isChecked = data.isChecked

				this.activeCar.sk.isChecked ? this.activeCar.sk.value = this.cars[index].sk : this.activeCar.sk.value = 0 
				this.finalPriceSum()
			},

			changeTTK(data) {
				let index = this.activeCar.index
				this.activeCar.ttk.isChecked = data.isChecked

				this.activeCar.ttk.isChecked ? this.activeCar.ttk.value = this.cars[index].ttk : this.activeCar.ttk.value = 0
				this.finalPriceSum()
			},

			onRelocationMoscow(data) {
				this.typeRelocation = data.title
				this.stateRelocation = data.state
				this.finalPriceSum()
			},

			onRelocationRussia(data) {
				this.typeRelocation = data.title
				this.stateRelocation = data.state
				this.finalPriceSum()
			},

			ValTime(data) {
				this.activeCar.valTime = data.value
				this.finalPriceSum()
			},
			ValMKAD(data) {
				this.activeCar.valMKAD = data.value
				this.finalPriceSum()
			},
			loaderChange(data) {
				this.loaderChecked = data.loaderChecked
				this.loaderTime = data.loaderTime
				this.finalPriceSum()
			},
			loaderTimeChange(data) {
				this.loaderTime = data.loaderTime
				this.finalPriceSum()
			},
			materialsChange(data) {
				this.materials = data.materials
				let showTitleMaterials
				let totalMaterialsPrice = 0

				this.materials.forEach(function(item, index){
					if(item.select > 0) {
						showTitleMaterials = true
					}
					totalMaterialsPrice += (item.price * item.select)
				});
				this.totalMaterialsPrice = totalMaterialsPrice
				this.showTitleMaterials = showTitleMaterials
				this.finalPriceSum()
			},
			cityDistanceChange(data) {
				this.currentCity.price = this.cars[this.activeCar.index].cities[data.city]
				this.finalPriceSum()
			},

			finalPriceSum() {
				if(this.stateRelocation == "moscow") {
					this.finalPrice = this.activeCar.min_car_price + (this.loaderTime * this.loaderChecked * this.oneLoaderPrice) + this.activeCar.ttk.value + this.activeCar.sk.value + ((this.activeCar.valTime * this.activeCar.worth_of_hour) - this.activeCar.min_hour * this.activeCar.worth_of_hour) + (this.activeCar.valMKAD * this.activeCar.mkad_price) + this.totalMaterialsPrice
				}
				else if (this.stateRelocation == "russia") {
					if(this.currentCity.name !== "") {
						this.finalPrice = this.currentCity.price + (this.loaderTime * this.loaderChecked * this.oneLoaderPrice) + this.totalMaterialsPrice
					}
					else {
						this.finalPrice = 0
					}


				}
			}
		},
		created: function() {
			this.carActive(0)
			this.finalPriceSum()
		}
	}
</script>

<style lang="scss">
@import 'assets/scss/_variables.scss';



#app {
	font-family: 'Avenir', Helvetica, Arial, sans-serif;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
	text-align: center;
	color: #2c3e50;
	margin-top: 20px;
	margin-bottom: 240px;
	display: flex;
	flex-wrap: wrap;

	.fade-enter-active, .fade-leave-active {
		transition: all .5s ease;
		max-height: 350px;
		opacity: 1;
	}
	.fade-enter, .fade-leave-to  {
		max-height: 0;
		transition: all .5s ease;
		opacity: 0;
	}

	.v-select {
		margin-bottom: 20px;
		width: 100%;
		background: linear-gradient(to bottom,#f6f6f6 0%,#ffffff 100%);
		&.single .selected-tag {
			position: absolute !important;
			font-size: 16px;
		}
		&.unsearchable {
			input[type=search] {
				width: 100% !important;
			}
		}

		.dropdown-toggle {
			padding: 3px 0 !important;
			border-radius: 0 !important;
			.clear {
				display: none;
			}
		}

		.open-indicator:before {
			position: relative;
			top: -1px;
			border-color: rgb(65, 184, 131);
		}

		.dropdown-menu {
			padding: 0 !important;
			overflow: hidden !important;
			background: #f2f2f2 !important;
			li {
				a {
					padding: 10px 10px;
				}
				&.highlight {
					a {
						background: $accent !important;
					}
				}
				&.active {
					a {
						pointer-events: none;
						background: $accent !important;
						color: #fff !important;
					}
					&.highlight {
						a {
							background: $accent !important;
						}
					}
				}
			}
		}
	}

	.number-input__button {
		background-color: $accent;
		height: 35px;
		min-width: 35px;
		width: 35px;
		border-radius: 0;
		position: relative;
		display: flex;
		align-items: center;
		justify-content: center;
		padding: 0;
		font-size: 23px;
		color: $black;
		border-bottom: 2px solid #357156;
		border-right: 2px solid #357156;
		font-weight: bold;

		&:after {
			width: 100% !important;
			height: 100% !important;
			background-color: transparent !important;
			content: '+';
			display: flex;
			align-items: center;
			justify-content: center;
		}

		&:before {
			width: 100% !important;
			height: 100% !important;
			background-color: transparent !important;
			display: none;
		}

		&:disabled {
			background-color: rgba($accent, .8);
			border-right: 0px solid #357156;
			border-bottom: 0px solid #357156;
			cursor: default;
			&:before,
			&:after{
				color: $black;
			}
		}

		&:active {
			border-right: 0px solid #357156;
			border-bottom: 0px solid #357156;
		}
	}

	.number-input__button--minus {

		&:before {
			content: '-';
			display: flex;
			align-items: center;
			justify-content: center;
			margin-top: -2.5px;
		}

		&:after {
			display: none;
		}
	}

	.number-input {
		display: flex;
		width: 135px;
		padding-bottom: 2px;
	}
	
	.number-input__input {
		padding: 0 !important;
		width: 100% !important;
		border: none!important;
		background-color: #fff;
		font-size: 20px;
	}
}

.calc__right {
	width: 40%;
}

.calc__left {
	width: 60%;
}

input {
	font-weight: bold;
	&:disabled {
		opacity: 1 !important;
		background: transparent !important;
		color: #000;
	}
}

.calc-car__right {
}



.vue-img-logo {
	font-size: 35px;
	font-weight: bold;
	width: 100%;
	margin: 0 0 20px;
	color: $black;
	mark {
		background: transparent;
		color: $accent;
	}
	img {
		height: 40px;
		width: auto;
	}
}



</style>



